.c-callout {
  position: relative;
  padding: 0 1rem;
  margin: 1rem 0;
  border-radius: 0.25rem;
}
html:not([dir="rtl"]) .c-callout {
  border-left: 4px solid #d8dbe0;
}
*[dir="rtl"] .c-callout {
  border-right: 4px solid #d8dbe0;
}
.c-callout-bordered {
  border: 1px solid #d8dbe0;
  border-left-width: 4px;
}
.c-callout code {
  border-radius: 0.25rem;
}
.c-callout h4 {
  margin-top: 0;
  margin-bottom: 0.25rem;
}
.c-callout p:last-child {
